import React from "react";

import { fullName } from "../utils/util";

function PersonalDetail({ personalDetails }: any) {
  const {
    currentDesignation,
    forename,
    middlename,
    surname,
    mobileNumber,
    email,
  } = personalDetails;

  return (
    <>
      <h2 className="name mt-0 mb-1 text-white text-uppercase text-uppercase">
        {fullName(forename, middlename, surname)}
      </h2>
      <div className="title mb-3">{currentDesignation}</div>
      <ul className="list-unstyled">
        {email ? (
          <li className="mb-2">
            <a href={`mailto:${email}`}>
              <i
                className="far fa-envelope fa-fw mr-2"
                data-fa-transform="grow-3"
              ></i>
              {email}
            </a>
          </li>
        ) : null}
        {mobileNumber ? (
          <li>
            <i
              className="fas fa-mobile-alt fa-fw mr-2"
              data-fa-transform="grow-6"
            ></i>
            {mobileNumber}
          </li>
        ) : null}
      </ul>
    </>
  );
}
