import React from "react";

function SocialNetworkDetail({ socialNetwork }: any) {
  const { linkedin, github, codepen, website } = socialNetwork;

  return (
    <ul className="resume-social list-unstyled">
      {linkedin ? (
        <li className="mb-3">
          <a href={linkedin} target="_blank" rel="noopener noreferrer">
            <span className="fa-container text-center mr-2">
              <i className="fab fa-linkedin-in fa-fw"></i>
            </span>
            {linkedin}
          </a>
        </li>
      ) : null}

      {github ? (
        <li className="mb-3">
          <a href={github} target="_blank" rel="noopener noreferrer">
            <span className="fa-container text-center mr-2">
              <i className="fab fa-github-alt fa-fw"></i>
            </span>
            {github}
          </a>
        </li>
      ) : null}

      {codepen ? (
        <li className="mb-3">
          <a href={codepen} target="_blank" rel="noopener noreferrer">
            <span className="fa-container text-center mr-2">
              <i className="fab fa-codepen fa-fw"></i>
            </span>
            {codepen}
          </a>
        </li>
      ) : null}
      {website ? (
        <li>
          <a href={website} target="_blank" rel="noopener noreferrer">
            <span className="fa-container text-center mr-2">
              <i className="fas fa-globe"></i>
            </span>
            {website}
          </a>
        </li>
      ) : null}
    </ul>
  );
}

export default SocialNetworkDetail;
