import { API_FAILURE, API_SUCCESS, IS_LOADING } from './actionType';

export const loader = (payload: any) => {

    return {
        type: IS_LOADING,
        payload
    }
}

export const fetchUserDetail = (payload: any) => {

    return {
        type: API_SUCCESS,
        payload: payload
    }
}

export const fetchUserDetailError = (payload: any) => {

    return {
        type: API_FAILURE,
        payload
    }
}