import { useEffect, useReducer } from "react";
import { PROFILE_URL } from "../config/api";
import fireStoreService from "../services/fireStoreService";
import {
  fetchUserDetail,
  fetchUserDetailError,
  loader,
} from "../store/actions";
import profileReducer, { initialState } from "../store/profileReducer";

function useFireStore() {
  const [state, dispatch] = useReducer(profileReducer, initialState);

  useEffect(() => {
    dispatch(loader(true));

    fireStoreService.collection(PROFILE_URL).onSnapshot(
      (querySnapshot) => {
        if (querySnapshot.empty) {
          dispatch(fetchUserDetail([]));
          return;
        }
        const data: any = {};
        querySnapshot.forEach((doc) => {
          data[doc.id] = doc.data();
        });

        dispatch(fetchUserDetail(data));
      },
      (error) => {
        dispatch(fetchUserDetailError(error));
      }
    );
  }, []);

  return state;
}

export default useFireStore;
