import React, { useContext } from "react";
import PersonalDetail from "../components/PersonalDetail";
import ProfileImage from "../components/ProfileImage";
import SocialNetworkDetail from "../components/SocialNetworkDetail";
import ProfileContext from "../store/ProfileContext";

function Header() {
  const { data } = useContext<any>(ProfileContext);

  const { personalDetails, socialNetwork } = data;

  return (
    <div className="media flex-column flex-md-row ">
      <ProfileImage />
      <div className="media-body p-4 d-flex flex-column flex-md-row mx-auto mx-lg-0">
        <div className="primary-info">
          <PersonalDetail personalDetails={personalDetails} />
        </div>
        <div className="secondary-info ml-md-auto mt-2">
          <SocialNetworkDetail socialNetwork={socialNetwork} />
        </div>
      </div>
    </div>
  );
}

export default Header;
