import { API_FAILURE, API_SUCCESS, IS_LOADING } from "./actionType";

export const initialState = {
    loading: false,
    data: [],
    error: []
}


const profileReducer = (state: any, action: any) => {

    switch (action.type) {

        case IS_LOADING:

            return { ...state, loading: action.payload }

        case API_SUCCESS:

            return { ...state, data: action.payload, erorr: [], loading: false }

        case API_FAILURE:

            return { ...state, data: [], erorr: action.payload, loading: false }

        default:

            return state;

    }
}

export default profileReducer;