import React, { useContext } from "react";
import ProfileContext from "../store/ProfileContext";
import { experienceYear, sortObject } from "../utils/util";

function EducationSummary() {
  const { data } = useContext<any>(ProfileContext);
  const { educationDetails } = data;
  const educationDetail = sortObject(educationDetails, "from", "desc");

  return (
    <section className="resume-section experience-section mb-5">
      <h2 className="resume-section-title text-uppercase font-weight-bold pb-3 mb-3">
        Education Detail
      </h2>
      <div className="resume-section-content">
        <div className="resume-timeline position-relative">
          {educationDetail.map((item, index) => (
            <article
              className="resume-timeline-item position-relative pb-5"
              key={index}
            >
              <div className="resume-timeline-item-header mb-2">
                <div className="d-flex flex-column flex-md-row">
                  <h3 className="resume-position-title font-weight-bold mb-2">
                    {item.courseName}
                  </h3>

                  <div className="resume-company-name ml-auto">
                    {item.institutionName}
                  </div>
                </div>
                <div className="d-flex flex-column flex-md-row">
                  <h6 className="resume-position-title  mb-2">
                    {item.institutionName}
                  </h6>
                </div>
                {item.location ? (
                  <div className="d-flex flex-column flex-md-row">
                    <h6 className="resume-position-title  mb-2">
                      {item.location}
                    </h6>
                  </div>
                ) : null}

                <div className="resume-position-time">
                  {experienceYear(item.from, item.to)}
                </div>
              </div>
              <hr />
            </article>
          ))}
        </div>
      </div>
    </section>
  );
}

export default EducationSummary;
