import React from "react";
import { HelmetProvider, Helmet } from "react-helmet-async";

function Seo({ children }: any) {
  return (
    <HelmetProvider>
      <Helmet>
        <meta charSet="utf-8" />
        <title>John Prakash Lobo</title>
        <meta name="description" content="John Prakash Lobo" />
        <link rel="canonical" href="" />
      </Helmet>
      {children}
    </HelmetProvider>
  );
}

export default Seo;
