import React, { useContext } from "react";
import ProfileContext from "../store/ProfileContext";
import { experienceYear, sortObject } from "../utils/util";

function WorkExperience() {
  const { data } = useContext<any>(ProfileContext);

  const { workExperience } = data;

  const workExp = sortObject(workExperience, "from", "desc");

  return (
    <section className="resume-section experience-section mb-5">
      <h2 className="resume-section-title text-uppercase font-weight-bold pb-3 mb-3">
        Work Experience
      </h2>
      <div className="resume-section-content">
        <div className="resume-timeline position-relative">
          {workExp.map((item, index) => (
            <article
              className="resume-timeline-item position-relative pb-5"
              key={index}
            >
              <div className="resume-timeline-item-header mb-2">
                <div className="d-flex flex-column flex-md-row">
                  <h3 className="resume-position-title font-weight-bold mb-2">
                    {item.companyName}
                  </h3>
                  <div className="resume-company-name ml-auto">
                    {item.companyName}
                  </div>
                </div>

                <div className="d-flex flex-column flex-md-row">
                  <h6 className="resume-position-title font-weight-bold mb-2">
                    {item.designation}
                  </h6>
                </div>
                {item.location ? (
                  <div className="d-flex flex-column flex-md-row">
                    <p className=" font-weight-bold mb-2">{item.location}</p>
                  </div>
                ) : null}

                <div className="resume-position-time">
                  {experienceYear(item.from, item.to)}
                </div>
              </div>
              <div className="resume-timeline-item-desc">
                {/*parse(item.description)*/}
                {item.technology.length && (
                  <>
                    <h4 className="resume-timeline-item-desc-heading font-weight-bold">
                      Technologies used:
                    </h4>

                    <ul className="list-inline">
                      {item.technology.map(
                        (technologyItem: any, index: any) => (
                          <li className="list-inline-item" key={index}>
                            <span className="badge badge-primary badge-pill">
                              {technologyItem}
                            </span>
                          </li>
                        )
                      )}
                    </ul>
                  </>
                )}
              </div>
              <hr />
            </article>
          ))}
        </div>
      </div>
    </section>
  );
}

export default WorkExperience;
