import React from "react";
import { RingLoader } from "react-spinners";
import { css } from "@emotion/react";

function Loader() {
  const override = css`
    display: block;
    margin: 0 auto;
    border-color: red;
  `;
  return <RingLoader size={500} color="#434E5E" css={override} />;
}

export default Loader;
