import firebase from 'firebase/app';
import 'firebase/firestore'
import { firbaseConfig } from '../config/firebase';

const initFirestore = () => {
    let firebaseInstance = null;
    try {

        firebaseInstance = firebase.initializeApp(firbaseConfig, "JohnLobo")

    } catch (error) {

        throw new Error(error)

    }
    return firebaseInstance;
}


const fireStoreService = firebase.apps.length ?
    firebase.app() : initFirestore()

export default fireStoreService.firestore();