import { orderBy, startCase } from "lodash";

export const sortObject = (data: any, key: string, sortOrder: any = "asc") => {

    return orderBy(
        data,
        function (item: any) {
            return item[key];
        },
        [sortOrder]
    );
}

export const timeStampToYear = (data: any) => {

    const date: any = new Date(data.toDate());

    return date.getFullYear();
}

export const experienceYear = (from: any, to: any) => {

    const fromYear = timeStampToYear(from)

    const toYear = !to ? "Present" : timeStampToYear(to)

    return fromYear + "-" + toYear;
}

export const fullName = (...names: string[]) => {

    return startCase(names.join(" "));

}